import React, { useState, useEffect } from "react";
import Rectangle88 from "../../assets/images/Rectangle 88.png";
import Ellipse24 from "../../assets/images/Ellipse 24.png";
import { useNavigate } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { Avatar, Badge, Input, Space, Radio, Button } from 'antd';
import { FaRegCalendarCheck } from "react-icons/fa";
import Sidebar from "../../components/sidebar";
import CustomModal from "../../components/custommodal"
import FormModal from "../../components/common/modal/form";
import SuccessModal from "../../components/common/modal/success";
import ImageUploader from "../../components/common/ImageUploader";

import { transactionsData, purchasedCasesData, chatProgressData, requestData } from '../../constant/data'

const RequesterDashboard = () => {
    const [active, setActive] = useState('REQUEST')
    const navigate = useNavigate();
    const { t, i18n } = useTranslation();

    useEffect(() => {
        console.log(active)
    }, [active])

    const handleActive = (current) => {
        // alert(current)
        setActive(current)
    }

    const [open, setOpen] = useState(false);
    const [popupdata, setPopupdata] = useState("");
    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);

    const [openConfirm, setOpenConfirm] = useState(false);
    const handleConfirm = () => setOpenConfirm(true);
    const handleConfirmClose = () => setOpenConfirm(false);

    const openPopUp = (data) => {
        setPopupdata(data);
        handleClose();
        handleConfirm();
    }
    const [openForm, setOpenForm] = useState(false);
    const [open1, setOpen1] = useState(false);
    const [chatRegister, setChatRegister] = useState(false);
    const hadnleOpenForm = () => setOpenForm(true);
    const hadnleCloseForm = () => setOpenForm(false);

    const handleOpen1 = () => setOpen1(true);
    const handleClose1 = () => setOpen1(false);

    const closefun = () => {
        hadnleCloseForm();
        handleOpen1();
    };


    return (
        <>
            {/* <Header page={"webscreens"} pagetitle={"Creator"} /> */}
            <section id="banner-section">
                <ImageUploader img={Rectangle88} alt='wait' isBack={true} />
                <div className="relative">
                    <div className="relative flex top-[-60px] md:justify-start md:left-20 justify-center">
                        <ImageUploader img={Ellipse24} alt='wait' isBack={false} />
                    </div>
                    <div className="md:relative md:top-[-100px] mt-[-20px] left-56">
                        <h1 className="m-0">@REQUEST_USER000</h1>
                        <p className="m-0">依頼専門。好きなジャンル➡サイバーパンク・和・等々</p>
                    </div>

                    <Button type="primary" className="flex md:relative md:mt-0 mt-10 md:top-[-70px] top-[-30px] left-32 text-white p-2 px-10 rounded-lg text-center">{t("CLIENT")}</Button>
                </div>

            </section>

            <section className="bg-gray-100 md:p-16 pl-16 pr-2 gap-2 justify-center py-20 grid grid-cols-12">

                <div className="md:col-span-3 col-span-1">
                    <Sidebar setCurrent={handleActive} />
                </div>
                <div className="md:col-span-9 col-span-11  shadow-md shadow-gray-200 bg-white overflow-scroll p-5 max-h-screen">
                    {active === 'REQUEST' &&
                        <div div >
                            <h1 className="border-b-7 border-blue-600 text-xl my-8 ml-3">{t("@REQUEST_USER000")}</h1>
                            <div className="flex gap-5 flex-wrap justify-center">
                                {requestData.map((request) => (
                                    <div key={request.id} onClick={() => { navigate(request.navigateTo) }} className="w-60 shadow-lg shadow-gray-500 relative">
                                        <img src={request.img} className="w-full"></img>
                                        <div className="px-4 text-xs py-1 bg-badge w-auto rounded-lg text-white text-center absolute top-2 left-2">
                                            <p className="font-semibold">REGUEST</p>
                                        </div>
                                    </div>
                                ))}
                            </div>
                        </div>

                    }
                    {active === 'CHAT' &&
                        <>
                            <h1 className="border-b-7 border-blue-600 text-xl my-8 ml-3">{t("CHAT LIST")}</h1>
                            {chatProgressData.map((chat) => (
                                <div key={chat.id} className=" cursor-pointer sm:items-center gap-5 shadow-md shadow-gray-300 mb-3 p-4 flex justify-between flex-col text-center pb-12 sm:flex-row sm:text-left sm:pb-0mb-5"
                                    onClick={() => { navigate('/progress') }}>
                                    <div className="flex md:flex-row items-center text-gray-700 gap-7 md:pl-5 pl-0 flex-col">
                                        <div className="">
                                            <Badge count={5} color="blue">
                                                <Avatar src={chat.imageUrl} size={'large'} />
                                                {/* <img className="w-30 h-30 rounded-full" src={chat.imageUrl} alt="wait" /> */}
                                            </Badge>
                                        </div>
                                        <div className="sub-progresschat-detial">
                                            <p className="sub-progresschat-detial-p">{chat.user}</p>
                                            <p>{chat.message}</p>
                                        </div>
                                    </div>
                                    <div className="sm:w-40">
                                        <span className="sub-progresschat-detial-span">{chat.date}</span>
                                    </div>
                                </div>
                            ))}
                        </>
                    }
                    {active === 'PROGRESS' &&
                        <div>
                            <h1 className="border-b-7 border-blue-600 text-xl my-8 ml-3">{t("PROGRESS")}</h1>
                            <div className=" md:p-8 p-3 bg-gray-50 flex flex-wrap justify-center gap-3 shadow-md shadow-gray-300">
                                {purchasedCasesData.map((caseItem) => (
                                    <div key={caseItem.id} className="bg-white shadow-md shadow-gray-500 p-5 rounded-md w-52">
                                        <h1 className="font-bold text-3xl mb-3">{caseItem.title}</h1>
                                        <div className="flex md:flex-row flex-col justify-between">
                                            <span className="font-semibold lg:text-2xl md:text-xl text-lg text-blue-500">{caseItem.count}</span>
                                            <span className="font-semibold lg:text-2xl md:text-xl text-lg text-blue-500">CASE</span>
                                        </div>

                                    </div>
                                ))}
                            </div>
                            <h1 className="mt-10 mb-5">{t("販売済み")}</h1>
                            <div>
                                {transactionsData.map((transaction) => (
                                    <div className="w-full flex shadow-sm shadow-gray-500 p-3 gap-5 lg:flex-row flex-col">
                                        <div className="flex flex-row lg:mb-5 items-center">
                                            <div className="flex lg:flex-row flex-col justify-between">
                                                <div className="flex">
                                                    <Badge count={1} color="blue" className="mr-2" />
                                                    <p className="">{transaction.name}</p>
                                                    <p className="">{transaction.title}</p>
                                                </div>
                                                <div className="flex justify-end">
                                                    <p className="date-last-box-chat">{transaction.time}</p>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="flex flex-row justify-around gap-3">
                                            <div className="chat-jp-name-price">
                                                <p className="chat-jp-name-price-1">支払金額</p>
                                                <p className="chat-jp-name-price-2">{transaction.amount}</p>
                                            </div>
                                            <div className="chat-jp-name-price">
                                                <p className="chat-jp-name-price-1">購入日</p>
                                                <p className="chat-jp-name-price-2">{transaction.date}</p>
                                            </div>
                                        </div>
                                        <div className="flex flex-row justify-around gap-3">
                                            <div className="chat-jp-name-price">
                                                <p className="chat-jp-name-price-1">納品予定日</p>
                                                <p className="chat-jp-name-price-2">{transaction.date}</p>
                                            </div>
                                            <div className="chat-jp-name-price">
                                                <p className="chat-jp-name-price-1">ステータス</p>
                                                <p className={`chat-jp-name-price-2 ${transaction.status === '納品完了' ? "" : "text-blue-500"} `}>{transaction.status}</p>
                                            </div>
                                        </div>
                                    </div>
                                ))}
                            </div>
                            <h1 className="mt-10 mb-5">{t("進行中")}</h1>
                            <div className="overflow-desiger-row">
                                {transactionsData.slice(1, 2).map((transaction) => (
                                    <div className="w-full flex shadow-sm shadow-gray-500 p-3 gap-5 lg:flex-row flex-col">
                                        <div className="flex flex-row lg:mb-5 items-center">
                                            <div className="flex lg:flex-row flex-col justify-between">
                                                <div className="flex">
                                                    <Badge count={1} color="blue" className="mr-2" />
                                                    <p className="">{transaction.name}</p>
                                                    <p className="">{transaction.title}</p>
                                                </div>
                                                <div className="flex justify-end">
                                                    <p className="date-last-box-chat">{transaction.time}</p>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="flex flex-row justify-around gap-3">
                                            <div className="chat-jp-name-price">
                                                <p className="chat-jp-name-price-1">支払金額</p>
                                                <p className="chat-jp-name-price-2">{transaction.amount}</p>
                                            </div>
                                            <div className="chat-jp-name-price">
                                                <p className="chat-jp-name-price-1">購入日</p>
                                                <p className="chat-jp-name-price-2">{transaction.date}</p>
                                            </div>
                                        </div>
                                        <div className="flex flex-row justify-around gap-3">
                                            <div className="chat-jp-name-price">
                                                <p className="chat-jp-name-price-1">納品予定日</p>
                                                <p className="chat-jp-name-price-2">{transaction.date}</p>
                                            </div>
                                            <div className="chat-jp-name-price">
                                                <p className="chat-jp-name-price-1">ステータス</p>
                                                <p className="chat-jp-name-price-2 text-blue-500">{transaction.status}</p>
                                            </div>
                                        </div>
                                    </div>
                                ))}
                            </div>
                            <h1 className="mt-10 mb-5">{t("納品完了")}</h1>
                            <div className="overflow-desiger-row">
                                {transactionsData.slice(0, 1).map((transaction) => (
                                    <div className="w-full flex shadow-sm shadow-gray-500 p-3 gap-5 lg:flex-row flex-col">
                                        <div className="flex flex-row lg:mb-5 items-center">
                                            <div className="flex lg:flex-row flex-col justify-between">
                                                <div className="flex">
                                                    <Badge count={1} color="blue" className="mr-2" />
                                                    <p className="">{transaction.name}</p>
                                                    <p className="">{transaction.title}</p>
                                                </div>
                                                <div className="flex justify-end">
                                                    <p className="date-last-box-chat">{transaction.time}</p>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="flex flex-row justify-around gap-3">
                                            <div className="chat-jp-name-price">
                                                <p className="chat-jp-name-price-1">支払金額</p>
                                                <p className="chat-jp-name-price-2">{transaction.amount}</p>
                                            </div>
                                            <div className="chat-jp-name-price">
                                                <p className="chat-jp-name-price-1">購入日</p>
                                                <p className="chat-jp-name-price-2">{transaction.date}</p>
                                            </div>
                                        </div>
                                        <div className="flex flex-row justify-around gap-3">
                                            <div className="chat-jp-name-price">
                                                <p className="chat-jp-name-price-1">納品予定日</p>
                                                <p className="chat-jp-name-price-2">{transaction.date}</p>
                                            </div>
                                            <div className="chat-jp-name-price">
                                                <p className="chat-jp-name-price-1">ステータス</p>
                                                <p className="chat-jp-name-price-2 text-blue-500">{transaction.status}</p>
                                            </div>
                                        </div>
                                    </div>
                                ))}
                            </div>
                        </div>
                    }
                    {active === 'MY ACCOUNT' &&
                        <div className="border border-solid border-gray-500 shadow-md bg-white md:p-7 p-2">

                            <h1 className="border-b-7 border-blue-600 text-xl p-8">{t("BANK ACCOUNTS")}</h1>
                            <div className="justify-center border border-solid border-gray-500 px-5">
                                <div className="py-5  text-gray-400">
                                    <div className="grid md:grid-cols-12 grid-row">
                                        <p className="md:col-span-3 col-span-12 text-left mt-2">種別</p>
                                        <div></div>
                                        <Radio.Group className="col-span-8">
                                            <Space direction="vertical" className="flex items-start">
                                                <Radio value={1}>銀行（ゆうちょ以外）・信用金庫など</Radio>
                                                <Radio value={2}>ゆうちょ銀行</Radio>
                                            </Space>
                                        </Radio.Group>
                                    </div>
                                    <hr className="my-2" />
                                    <div className="grid md:grid-cols-12 grid-row">
                                        <p className="md:col-span-3 col-span-12 text-left mt-2">金融機関・支店</p>
                                        <div></div>
                                        <p className="col-span-8 text-left p-2">銀行（ゆうちょ以外）・信用金庫など </p>
                                    </div>
                                    <hr className="my-2" />
                                    <div className="grid md:grid-cols-12 grid-row">
                                        <p className="md:col-span-3 col-span-12 text-left mt-2">口座種別</p>
                                        <div></div>
                                        <Radio.Group className="col-span-8">
                                            <Space direction="vertical" className="flex items-start">
                                                <Radio value={1}>普通</Radio>
                                                <Radio value={2}>当座</Radio>
                                            </Space>
                                        </Radio.Group>
                                    </div>
                                    <div className="grid md:grid-cols-12 grid-row">
                                        <p className="md:col-span-3 col-span-12 text-left mt-2">口座番号</p>
                                        <div></div>
                                        <div className="col-span-8 mt-2 mb-2">
                                            <Input className="w-full border rounded-sm p-2 mb-2" type="text" id="html" name="fav_language" />
                                            <p className="m-0 text-xs">数字7桁・6桁の場合は、先頭に0をつけてください。 </p>
                                        </div>
                                    </div>
                                    <div className="grid md:grid-cols-12 grid-row">
                                        <p className="md:col-span-3 col-span-12 text-left mt-2">口座名義人</p>
                                        <div></div>
                                        <div className="col-span-8 mt-2 mb-2">
                                            <Input className="w-full border rounded-sm p-2 mb-2" type="text" id="html" name="fav_language" />
                                            <p className="m-0 text-xs">全角カタカナ　32文字以内 </p>
                                        </div>
                                    </div>
                                    <Button
                                        // onClick={() => { openPopUp("Account registration completed") }}
                                        className="w-full p-5 my-5" type="primary"
                                        icon={<FaRegCalendarCheck />}
                                    >
                                        {t("Register an account")}
                                    </Button>
                                </div>
                            </div>
                            <p className="text-center text-[#F4003A] mt-5 mb-5">{t("Payment will be made on the 15th of the following month (tentative), closing at the end of the month.")}</p>
                        </div>

                    }
                </div>
            </section >
            <CustomModal open={open} handleClose={handleClose}>
                <div>
                    <p className="text-badge font-bold">{t("Edit Username")}</p>
                    <input
                        className="p-2 w-[90%] border border-gray-400"
                        type="text"
                        placeholder="例）USER_00000"
                    />
                    <p className="text-badge font-bold">{t("Edit Your Introduction")}</p>
                    <textarea
                        className="p-2 w-[90%] border border-gray-400 h-[150px]"
                        type="text"
                        placeholder="例）はじめまして。好きなジャンルは〇〇です。"
                    />
                    <button
                        onClick={() => { openPopUp("Registration of profile has been completed") }}
                        className="register-popup-value"
                    >
                        {t("Register")}
                    </button>
                </div>
            </CustomModal>
            <CustomModal open={open} handleClose={handleClose}>
                <div>
                    <h1 className="text-lg text-badge font-bold">ユーザーネームを編集する</h1>
                    <input className="p-4 w-full border border-solid border-gray-800" type="text" placeholder="例）USER_00000" />
                    <h1 className="text-lg text-badge font-bold mt-3">自己紹介を編集する</h1>
                    <textarea className="w-full h-40 border border-solid border-gray-800 p-4" placeholder="例）はじめまして。好きなジャンルは〇〇です。" />
                    <p>あと100文字（全角）</p>
                    <button onClick={() => { handleClose() }} className="w-1/3 p-2.5 border-none bg-gray-800 text-white rounded-md block mx-auto mt-6 hover:opacity-50 duration-500 shadow-md shadow-gray-500">登録する</button>
                </div>
            </CustomModal>
            <CustomModal open={openConfirm} handleClose={handleConfirmClose}>
                <div>
                    <p className="text-center">{t(popupdata)}</p>
                </div>
            </CustomModal>
            <FormModal open={openForm} onCancel={hadnleOpenForm} onConfirm={closefun} />
            <SuccessModal open={open1}
                onCancel={handleClose1}
                title="定型文の登録が完了しました。"
            />
        </>
    );
}

export default RequesterDashboard;


